.comp-daily .section-inner {
  padding-top: 0 !important;
}
.comp-daily .section-inner ul li {
  padding: 5px;
  border-bottom: 1px solid rgba(142, 209, 241, 0.5);
}
.comp-daily .section-inner ul li a {
  position: relative;
  display: block;
  padding: 10px;
  transition: all 0.3s;
}
.comp-daily .section-inner ul li a .fa-caret-down {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1.3em;
  transition: all 0.3s;
}
.comp-daily .section-inner ul li a .date {
  font-weight: 550;
}
.comp-daily .section-inner ul li a .date span {
  font-weight: 600;
}
.comp-daily .section-inner ul li a .date span.desc {
  font-style: italic;
}
.comp-daily .section-inner ul li a .info img {
  display: inline-block;
  margin-right: 15px;
  vertical-align: middle;
  width: 50px;
}
.comp-daily .section-inner ul li a .info img span {
  margin-right: 15px;
}
.comp-daily .section-inner ul li a .info img span em {
  font-weight: 500;
}
.comp-daily .section-inner ul li a .info img span em i {
  margin-right: 5px;
}
.comp-daily .section-inner ul li a.active {
  background: rgba(255, 255, 255, 0.5);
  color: var(--color1);
  border-radius: 5px;
}
.comp-daily .section-inner ul li a.active .fa-caret-down {
  transform: translateY(-50%) rotate(180deg);
}
.comp-daily .section-inner ul li .detail {
  display: none;
  color: #fff;
  background: rgba(67, 120, 163, 0.5);
  padding: 10px;
  border-radius: 5px;
}
.comp-daily .section-inner ul li .detail p {
  margin: 0 5px;
  padding: 7px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}
.comp-daily .section-inner ul li .detail p:last-child {
  border-bottom: none;
}
.comp-daily .section-inner ul li .detail span {
  margin-right: 15px;
}
.comp-daily .section-inner ul li .detail span em {
  font-weight: 600;
}
.comp-daily .section-inner ul li .detail span em small {
  font-size: 0.85em;
}