@use "mixin" as m;

.comp-hourly {
    .section-inner {
        padding-top: 0 !important;
        overflow: auto;
        h2 {
            position: sticky;
            left: 0;
        }
        @media screen and (min-width: 900px) {
            display: flex;
            flex-direction: column;
        } //min 900 section-inner
        .wrap {
            position: relative;
            display: inline-table; //자식크기에 맞춰 알아서 늘어남
            @media screen and (min-width: 900px) {
               //height: calc(100% - 25px);
               height: 100%;
            } //wrap max 900
            @media screen and (max-width: 900px) {
                height: 400px;
            } //wrap max 900
            ul {
                display: inline-flex; //같이 늘어나야 돼서...
                height: 100%;
                li {
                    padding-top: 20px;
                    box-sizing: border-box;
                    width: 70px;
                    height: 100%;
                    &:nth-child(even) {
                        background: rgba(#fff, 0.5);
                    }
                    p {
                        margin: 3px 0;
                        font-size: 0.9em;
                        img {
                            display: inline-block;
                            width: 70%;
                        } //img
                        text-align: center;
                    } //p
                } //li
            } //ul
            .chart-wrap {
                position: absolute;
                width: calc(100% - 35px);
                bottom: 0;
                left: 5px;
                height: 50%;
                background: #fff;
            } //chart-wrap
        } //wrap
    } //section-inner
} //comp-hourly
