@use "mixin" as m;

.comp-daily {
    .section-inner {
        padding-top: 0 !important;
            ul {
                li {
                    padding: 5px;
                    border-bottom: 1px solid rgba(#8ed1f1, 0.5);
                    a {
                        position: relative;
                        display: block;
                        padding: 10px;
                        transition: all 0.3s;
                        
                        .fa-caret-down {
                            position: absolute;
                            right: 10px;
                            top: 50%;
                            transform: translateY(-50%);
                            font-size: 1.3em;
                            transition: all 0.3s;
                        }//i down
                        .date {
                            font-weight: 550;
                            span {
                                font-weight: 600;
                                &.desc {
                                    font-style: italic;
                                }//span.desc
                            } //span
                        } //date

                        .info{
                            img{
                                display: inline-block;
                                margin-right: 15px;
                                vertical-align: middle;
                                width: 50px;
                                span{
                                    margin-right: 15px;
                                  em{
                                      font-weight: 500;
                                    i{
                                        margin-right: 5px;
                                    }
                                    }
                                }//span
                            }//img
                        }//info
                        &.active{
                        background: rgba(#FFF,0.5);
                        color: var(--color1);
                        border-radius: 5px;
                        .fa-caret-down{
                            transform: translateY(-50%) rotate(180deg);
                        }
                        }
                    } //a
                    .detail {
                        display: none;
                        color: #fff;
                        background: rgba(#4378a3, 0.5);
                        padding: 10px;
                        border-radius: 5px;
                        p{
                            margin: 0 5px;
                            padding: 7px 0;
                            border-bottom: 1px solid rgba(#FFF,0.5);
                            &:last-child{border-bottom: none;}
                        }
                        span{
                            margin-right: 15px;                        
                            em{
                                font-weight: 600;
                                small{
                                    font-size: 0.85em;
                                }//small
                            
                            }//em
                        }//span
                    } //detail
                } //li
            } //ul
    } //section-inner
} //comp-daily
