@use "mixin" as m;

section.comp-current {
    @media screen and (max-width: 900px) {
        margin-top: 30px !important;
    } //모바일

    .section-inner {
        .current-output {
            position: relative;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            box-sizing: border-box;
            border-radius: 10px;
            background-position: center;
            background-size: cover; //background에 색상을 넣어두면 적용이 안됨
            color: #fff;
            @media screen and (min-width: 900px) {
                height: 100%;
            } //section-inner min 900
            @media screen and (max-width: 900px) {
                padding: 10px;
            } //section-inner min 900

            button.reload{
                z-index: 2;
                position: absolute;
                width: 50px;
                height: 50px;
                right: 5%; top: 15%;
                font-size: 1.1em;
                opacity: 0.7;
                transition: all 0.5s;
                &:active, &:hover{
                    opacity: 1;
                    transform: rotate(180deg);
                }
            }

            address {
                display: flex;
                overflow: hidden;
                //white-space: nowrap; 이렇게 해도 되지만... 블럭레벨 간 간격이 생겨서 애니메이션 할 때 안 맞을 수 있어서 간격없이 딱붙는 flex 함
                margin-bottom: 10px;
                width: min(90%, 350px);
                box-sizing: border-box;
                padding: 5px;
                span {
                    display: inline-block;
                    white-space: nowrap; //줄내림 막음
                    font-size: 1.1em;
                    font-weight: 500;
                    animation: marquee 15s infinite linear;
                    @keyframes marquee {
                        0% {
                            transform: translateX(0%);
                        }
                        100% {
                            transform: translateX(-100%);
                        }
                    } //animation marquee
                } //span
            } //address
            .circle-container {
                position: relative;
                border-radius: 50%;
                background: rgba(#000, 0.2);
                width: min(85%, 300px);
                @include m.square;
                display: flex;
                align-items: center;
                justify-content: center;
                .sunset {
                    position: absolute;
                    width: 100%;
                    height: 50%;
                    left: 0;
                    top: 0;
                    svg {
                        width: 100%;
                        /* path================================== */
                        .line {
                            fill: none;
                            stroke: rgba(#fff, 0.5);
                            stroke-width: 3px;
                        }
                        .sun {
                            fill: rgb(255, 196, 0);
                            stroke: rgba(255, 196, 0, 0.3);
                            offset-path: path("M25,200c0-96.65,78.35-175,175-175s175,78.35,175,175");
                            //offset-distance: 50%;
                        } //sun
                        .dash {
                            fill: none;
                            stroke:#232d2d;
                            stroke-width: 6px;
                            stroke-dasharray: 1;
                            //stroke-dashoffset: 0.5;
                            //1일 때 해 짐 0.5 정오 0은 해 짐
                        }
                        /* path================================== */
                    } //svg
                    time {
                        font-size: 0.85em;
                        background: rgb(255, 196, 0);
                        border-radius: 10px;
                        padding: 3px 5px;
                        display: inline-block;
                        small {
                            font-size: 0.8em;
                            font-weight: 600;
                            margin-right: 2px;
                        }
                        &.sunrise-time {
                            transform: translateX(-30%);
                        }
                        &.sunset-time {
                            float: right;
                            transform: translateX(30%);
                        }
                    } //time
                } //sunset
                .circle-container-inner {
                    @include m.posLT(absolute, 50%, 50%, -50%, -50%);
                    width: 90%;
                    .temp {
                        position: relative;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        span{
                         position: absolute;   
                         transform: translate(40px,10px);
                         font-weight: 600;
                         text-shadow: 1px 1px 0 var(--color1);
                         i{
                            margin-right: 3px;
                            
                         }
                        }
                        em {
                            font-size: 2.5em;
                            margin-right: -50px;
                            position: relative;
                            z-index: 1;
                            font-weight: 400;
                            text-shadow: 1px 1px 0 var(--color1);
                            sup{font-size: 0.7em;}
                        }
                        img {
                            width: 100px;
                            mask-image: url(../assets/img/mask.png);
                            mask-size: 100%;
                            mask-position: center;
                            mask-repeat: no-repeat;
                        }

                    } //temp

                    .desc{
                        text-align: center;
                        font-size: 1.2em;
                        margin-top: -10px;
                    }
                    .etc{
                        background: rgba(#000,0.3);
                        text-align: center;
                        width: 70%;
                        margin: 1px auto;
                        box-sizing: border-box;
                        padding: 5px;
                        border-radius: 10px;
                        span{
                            display: inline-block;
                            margin: 1px 7px;
                            font-size: 0.9em;
                                i{
                                    &.fa-location-arrow{
                                        margin-right: 3px;
                                        font-size: 0.8em;
                                    }
                                    margin-right: 3px;
                                    font-size: 0.8em;
                                }
                                small{
                                    margin-left: 2px;
                                    font-size: 0.7em;
                                }
                        }//span
                    }//etc

                    .time-date{
                        text-align: center;
                        color: #FFF;
                        time{
                            display: block;
                            &.time{
                                font-size: 1.3em;
                            }
                            small{
                                font-size: 0.7em;
                            }
                        }

                    }
                } //circle-container-inner
            } //circle-container

        } //current-output
    } //section-inner
} //section.comp-current
