@use 'mixin' as m;

.comp-map{
    
            @media screen and (max-width:900px) {
                position: fixed;
                top: 0; left: 0;
                width: 100%;
                height: 100%;
                z-index: 10;
                margin: 0 !important;
                font-size: 1.5em;
                transform:translateX(100%);
                transition: transform 0.5s;
                &.active{
                    transform: translateX(0%);
                    background: rgba(#FFF,0.9);
                    border-radius: 10px;
                
                }
            }//comp-map max 900
            .btns{
                display: flex;
                position: absolute;
                top: calc(50% - 25px);
                left: -50px;
                width: 100px;
                height: 50px;                
                z-index: 1;
                border-radius: 5px;
                overflow: hidden;
                button{
                    width: 50px;
                    height: 50px;
                    color: #FFF;
                    &:first-child{
                        background: orange;
                    }
                    &:last-child{
                        background: orangered;
                    }
                }//button
            @media screen and (min-width:900px) {
                display: none;
            }//section-inner min 900
            }//btns

    .section-inner{
        @media screen and (max-width:900px) {
            height: 100%;
        }//max inner
        #map{
            //filter: hue-rotate(40deg);
            height: 100%;
            border-radius: 10px;
        }//#map
    }//section-inner
}//comp-map