html {
  --sky: #8ed1f1;
  --color1: #4378a3;
  --color2: #4b87b6;
}

body {
  color: var(--color1);
  font-family: "Montserrat", "Noto Sans KR", sans-serif;
  font-size: 15px;
  text-decoration: none;
  font-weight: normal;
  font-style: normal;
}

body {
  height: 100vh;
  background: var(--sky);
}
body::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(../assets/img/weather-icos.gif);
  background-size: 900px;
  opacity: 0.3;
  animation: bg 5000s linear infinite;
}
@keyframes bg {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: -50000px -50000px;
  }
}

h1 {
  z-index: 1;
  perspective: 700;
  position: absolute;
  left: 50%;
  top: 0%;
  width: min(40%, 200px);
  transform: translate(-50%, -50%);
}
h1 img {
  width: 100%;
  opacity: 0.7;
  animation: rotate 5s infinite linear;
}
@keyframes rotate {
  0% {
    transform: rotateX(0deg);
  }
  30% {
    transform: rotateX(360deg);
  }
  100% {
    transform: rotateX(360deg);
  }
}

#root {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -48%);
  width: min(95%, 1100px);
  height: min(90%, 800px);
  background: rgba(255, 255, 255, 0.7);
  border-radius: 15px;
  padding: 5px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  animation: grow 1.5s infinite alternate linear;
}
@media screen and (min-width: 900px) {
  #root {
    min-height: 760px;
  }
}
@keyframes grow {
  0% {
    box-shadow: 0 0 10px 0 rgba(255, 255, 255, 0.3);
  }
  100% {
    box-shadow: 0 0 10px 5px rgba(255, 255, 255, 0.7);
  }
}
#root > div.app-inner {
  height: 100%;
}
@media screen and (min-width: 900px) {
  #root > div.app-inner {
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
  }
}
@media screen and (max-width: 900px) {
  #root > div.app-inner {
    overflow: hidden;
    overflow-y: auto;
    overflow-x: hidden;
  }
}
#root > div.app-inner section {
  box-sizing: border-box;
}
@media screen and (min-width: 900px) {
  #root > div.app-inner section {
    width: 50%;
    height: 50%;
  }
}
@media screen and (max-width: 900px) {
  #root > div.app-inner section {
    margin: 20px 0;
  }
}
#root > div.app-inner section .section-inner {
  position: relative;
  border-radius: 10px;
  padding: 10px;
  box-sizing: border-box;
  min-height: 250px;
}
@media screen and (min-width: 900px) {
  #root > div.app-inner section .section-inner {
    overflow: auto;
    width: 100%;
    height: 100%;
  }
}
#root > div.app-inner section .section-inner .preloader {
  border-radius: 10px;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: var(--sky);
  display: flex;
  align-items: center;
  justify-content: center;
}
#root > div.app-inner section .section-inner h2 {
  text-transform: uppercase;
  font-weight: 600;
  box-sizing: border-box;
  padding: 5px 10px;
  background: var(--color1);
  color: #FFF;
  border-radius: 10px;
}
@media screen and (min-width: 900px) {
  #root > div.app-inner section .section-inner h2 {
    position: sticky;
    top: 0px;
    left: 0px;
    z-index: 1;
  }
}

@media (hover: hover) {
  body, .app-inner, .section-inner {
    border-radius: 5px;
  }
  body::-webkit-scrollbar-track, .app-inner::-webkit-scrollbar-track, .section-inner::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
    background-color: #fff;
  }
  body::-webkit-scrollbar, .app-inner::-webkit-scrollbar, .section-inner::-webkit-scrollbar {
    width: 6px;
    height: 6px;
    background-color: #f5f5f5;
  }
  body::-webkit-scrollbar-thumb, .app-inner::-webkit-scrollbar-thumb, .section-inner::-webkit-scrollbar-thumb {
    background-color: var(--color2);
  }
}

#root,
.app-inner {
  border-radius: 5px;
}
@media (hover: hover) {
  #root::-webkit-scrollbar-track,
  .app-inner::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
    background-color: #fff;
  }
  #root::-webkit-scrollbar,
  .app-inner::-webkit-scrollbar {
    width: 6px;
    height: 6px;
    background-color: #f5f5f5;
  }
  #root::-webkit-scrollbar-thumb,
  .app-inner::-webkit-scrollbar-thumb {
    background-color: var(--color2);
  }
}

/* spinner==================================================================== */
.loader {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 6rem;
  margin-top: 3rem;
  margin-bottom: 3rem;
}

.loader:before,
.loader:after {
  content: "";
  position: absolute;
  border-radius: 50%;
  animation: pulsOut 1.8s ease-in-out infinite;
  filter: drop-shadow(0 0 1rem rgba(255, 255, 255, 0.75));
}

.loader:before {
  width: 100%;
  padding-bottom: 100%;
  box-shadow: inset 0 0 0 1rem #fff;
  animation-name: pulsIn;
}

.loader:after {
  width: calc(100% - 2rem);
  padding-bottom: calc(100% - 2rem);
  box-shadow: 0 0 0 0 #fff;
}

@keyframes pulsIn {
  0% {
    box-shadow: inset 0 0 0 1rem #fff;
    opacity: 1;
  }
  50%, 100% {
    box-shadow: inset 0 0 0 0 #fff;
    opacity: 0;
  }
}
@keyframes pulsOut {
  0%, 50% {
    box-shadow: 0 0 0 0 #fff;
    opacity: 0;
  }
  100% {
    box-shadow: 0 0 0 1rem #fff;
    opacity: 1;
  }
}